@use "../base/variables.scss" as var;
@use "../base/mixins.scss" as *;

input[type="text"],
input[type="password"],
input[type="number"],
input[type="email"],
textarea,
.input-text {
    border-radius: 4px;
    border: 1px solid var(--input-bdc, var.$inputDefault);

    font-size: var(--body1);
    // letter-spacing: -0.28px;
    color: var(--input-c, var.$b333);

    background-color: var(--input-bgc, var.$bfff);
    &::placeholder {
        color: var.$b999;
    }
    &:hover:not(:disabled):not(:read-only),
    &:focus:not(:disabled):not(:read-only) {
        --input-bdc: #{var.$inputFocus};
    }
    &.is-error,
    &:invalid {
        --input-bdc: #{var.$p_red};
        & ~ .feedback {
            --typo-custom-c: #{var.$p_red};
        }
    }
    &:disabled {
        --input-bgc: #{var.$bgHeavy};
    }
}
input[type="text"],
input[type="password"],
input[type="number"],
input[type="email"],
.input-text {
    width: var(--input-width, 100%);
    height: var(--input-height, var(--item-height));
    padding: 0 var(--input-px, 12px);

    &[type="number"]{
        -webkit-appearance: textfield;
        appearance: textfield;
    }
}
input[type="number"]{
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        appearance: none;
        margin: 0;
    }
}
input.date {
    padding-right: 36px;
    background-image: url('../image/icon/calendar.svg');
    background-size: 16px;
    background-repeat: no-repeat;
    background-position: calc(100% - 12px) center;
    // @include ellipse;
}

